import {HttpClient, HttpClientModule} from '@angular/common/http';
import {ErrorHandler, NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {Camera} from '@ionic-native/camera';
import {SplashScreen} from '@ionic-native/splash-screen';
import {StatusBar} from '@ionic-native/status-bar';
import {IonicStorageModule, Storage} from '@ionic/storage';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';


import {Items} from '../mocks/providers/items';
import {Api, Settings, User} from '../providers/providers';
import {MyApp} from './app.component';
import {NgArrayPipesModule} from "ngx-pipes/esm";

// The translate loader needs to know where to load i18n files
// in Ionic's static asset pipeline.
export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

export function provideSettings(storage: Storage) {
  /**
   * The Settings provider takes a set of default settings for your app.
   *
   * You can add new settings options at any time. Once the settings are saved,
   * these values will not overwrite the saved values (this can be done manually if desired).
   */
  return new Settings(storage, {
    option1: true,
    option2: true,
    option3: 'Facebook',
    option4: 'I want to learn to cook!'
  });
}

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    NgArrayPipesModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/fave/fave.module.ngfactory#FavePageModuleNgFactory', name: 'FavePage', segment: 'fave', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/content/content.module.ngfactory#ContentPageModuleNgFactory', name: 'ContentPage', segment: 'content', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/item-create/item-create.module.ngfactory#ItemCreatePageModuleNgFactory', name: 'ItemCreatePage', segment: 'item-create', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list-master/list-master.module.ngfactory#ListMasterPageModuleNgFactory', name: 'ListMasterPage', segment: 'list-master', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/item-detail/item-detail.module.ngfactory#ItemDetailPageModuleNgFactory', name: 'ItemDetailPage', segment: 'item-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/menu/menu.module.ngfactory#MenuPageModuleNgFactory', name: 'MenuPage', segment: 'menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notification/notification.module.ngfactory#NotificationPageModuleNgFactory', name: 'NotificationPage', segment: 'notification', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/search/search.module.ngfactory#SearchPageModuleNgFactory', name: 'SearchPage', segment: 'search', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module.ngfactory#SettingsPageModuleNgFactory', name: 'SettingsPage', segment: 'settings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module.ngfactory#SignupPageModuleNgFactory', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsPageModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/welcome/welcome.module.ngfactory#WelcomePageModuleNgFactory', name: 'WelcomePage', segment: 'welcome', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    Api,
    Items,
    User,
    Camera,
    SplashScreen,
    StatusBar,
    { provide: Settings, useFactory: provideSettings, deps: [Storage] },
    // Keep this to enable Ionic's runtime error handling during development
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



